import React, {
  useCallback,
  useEffect,
  useMemo,
  useState,
  useRef,
} from "react";
import { useDispatch, useSelector } from "react-redux";
import CardComponent from "../../components/Card/Card";
import { Button, Form, Image } from "react-bootstrap";
import "./companionWidget.css";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import store from "../../store";
import { refreshWidgetData } from "../../services/api/refreshService";
import ConfirmationModal from "../../components/Modals/ConfirmationModal";

import { FaRegCopy } from "react-icons/fa";
import {
  setCAItemDetails,
  setCAItemObjectDetails,
  setDroppedObjectData,
  setIsDropped,
  setPlantObjectData,
} from "../../store/droppedObjectSlice";
import {
  formattedFinalMessage,
  getCardData,
  getTableData,
  getUniqueTableData,
  processErrorObj,
  tableColumns,
  uniqueColumns,
} from "./dataHelpers";
import useToast, { useToastWithProgress } from "../../hooks/useToast";
// import { MSG_WIDGET_RESET_SUCCESS } from "../../utils/toastMessages";
import useCompanianDropableArea from "../../hooks/useCompanianDropableArea";
import {
  handleAddData,
  handleRemoveData,
  saveData,
} from "../../services/api/PlantAssignment/saveTableDataService";
import CompanionToolbarNativeCta from "./companionToolbarNativeCta";
import { MSG_SAVE_FAILURE, MSG_SAVE_SUCCESS } from "../../utils/toastMessages";
import DragAndDropComponent from "./DragAndDrop";
import * as XLSX from "xlsx";
import ContentErrorsModal from "../../components/Modals/ContentErrorsModal";
import { getAllPlants } from "../../services/api/PlantAssignment/allPlantSevice";
import { noPlants } from "../../services/api/PlantAssignment/noPlants";
import axios from "axios";
import { fetchCsrfToken } from "../../services/api/PlantAssignment/fetchCsrfService";
import {
  handleFileChange,
  processManufacturingCA,
} from "../../services/api/PlantAssignment/createMFGCA";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRotateLeft } from "@fortawesome/free-solid-svg-icons"; // This is the reset-style icon
import { fetchData } from "../../utils/helpers";
import { setSelectedTableRows } from "../../store/droppedObjectSlice";

const CompanionWidget = () => {
  // ...state and refs
  const [showConfirmationModal, setShowConfirmationModal] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [file, setFile] = useState(null);
  const [errors, setErrors] = useState([]);
  const [successMessage, setSuccessMessage] = useState("");
  const [failureMessage, setFailureMessage] = useState("");
  const [plantData, setPlantData] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [tableData, setTableData] = useState([]);
  const [uniqueTableData, setUniqueTableData] = useState([]);
  const [cardData, setCardData] = useState([]);
  const [droppedData, setDroppedData] = useState([]);
  const [isDropped, setIsDropped] = useState(false);
  const [resetKey, setResetKey] = useState(Date.now());
  const [showContentErrorsModal, setShowContentErrorsModal] = useState(false);
  const [contentErrors, setContentErrors] = useState([]);
  const dispatch = useDispatch();
  const {
    caItemDetails,
    caItemObjectDetails,
    droppedObjectData,
    plantObjectData,
  } = useSelector((state) => state.droppedObject);

  // Add updateTableData for Table meta prop
  const updateTableData = (updatedData) => {
    setTableData(updatedData);
  };

  // ...callbacks and effects
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setFile(file);
      const reader = new FileReader();
      reader.onload = (evt) => {
        const bstr = evt.target.result;
        const workbook = XLSX.read(bstr, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
        console.log(jsonData);
      };
      reader.readAsBinaryString(file);
    }
  };

  const processFile = () => {
    if (!file) return;
    setIsLoading(true);
    const formData = new FormData();
    formData.append("file", file);
    axios
      .post("/api/process-file", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((response) => {
        setIsLoading(false);
        setSuccessMessage("File processed successfully!");
        setErrors([]);
        setContentErrors([]);
        setShowContentErrorsModal(true);
        // Process response data
        const { plantData, cardData, tableData } = response.data;
        setPlantData(plantData);
        setCardData(cardData);
        setTableData(tableData);
        // Dispatch actions or update state as needed
        dispatch(setPlantObjectData(plantData));
        dispatch(setCAItemDetails(cardData));
        dispatch(setDroppedObjectData(tableData));
      })
      .catch((error) => {
        setIsLoading(false);
        setFailureMessage("Error processing file.");
        setSuccessMessage("");
        setErrors([]);
        setContentErrors([]);
        setShowContentErrorsModal(true);
        console.error("Error processing file:", error);
      });
  };

  const handleSave = () => {
    setIsLoading(true);
    saveData({ tableData, cardData })
      .then((response) => {
        setIsLoading(false);
        setSuccessMessage("Data saved successfully!");
        setFailureMessage("");
        setErrors([]);
        setContentErrors([]);
        setShowContentErrorsModal(true);
      })
      .catch((error) => {
        setIsLoading(false);
        setFailureMessage("Error saving data.");
        setSuccessMessage("");
        setErrors([]);
        setContentErrors([]);
        setShowContentErrorsModal(true);
        console.error("Error saving data:", error);
      });
  };

  const handleReset = () => {
    setResetKey(Date.now());
    setFile(null);
    setErrors([]);
    setSuccessMessage("");
    setFailureMessage("");
    setPlantData([]);
    setCardData([]);
    setTableData([]);
    dispatch(setPlantObjectData([]));
    dispatch(setCAItemDetails([]));
    dispatch(setDroppedObjectData([]));
  };

  const handleRowSelect = (selectedRowKeys, selectedRows) => {
    setSelectedRows(selectedRows);
    dispatch(setSelectedTableRows(selectedRows));
  };

  // ...memoized values
  const memoizedCardData = useMemo(() => {
    return cardData.map((item) => ({
      ...item,
      key: item.id,
    }));
  }, [cardData]);

  const memoizedTableData = useMemo(() => {
    return tableData.map((item) => ({
      ...item,
      key: item.id,
    }));
  }, [tableData]);

  // ...effects
  useEffect(() => {
    // Initial data fetch or setup
    refreshWidgetData()
      .then((data) => {
        // Handle successful data fetch
        const { plantData, cardData, tableData } = data;
        setPlantData(plantData);
        setCardData(cardData);
        setTableData(tableData);
        dispatch(setPlantObjectData(plantData));
        dispatch(setCAItemDetails(cardData));
        dispatch(setDroppedObjectData(tableData));
      })
      .catch((error) => {
        // Handle error
        console.error("Error fetching data:", error);
      });
  }, [dispatch]);

  return (
    <div className="companion-widget">
      <div className="widget-header">
        <h2>Companion Widget</h2>
        <Button
          variant="primary"
          onClick={() => setShowConfirmationModal(true)}
        >
          Open Confirmation Modal
        </Button>
      </div>
      <div className="widget-content">
        <CardComponent
          title="Card Title"
          content="Card content goes here."
          footer="Card footer"
        />
        <ReusableTable
          columns={tableColumns}
          data={memoizedTableData}
          onRowSelect={handleRowSelect}
          meta={{ updateTableData }}
          widgetType="Companion_Widget"
        />
        <CardWithDragAndDrop
          data={droppedObjectData}
          onDrop={(item) => {
            setDroppedData((prev) => [...prev, item]);
            setIsDropped(true);
          }}
          onDragEnd={() => setIsDropped(false)}
        />
        <div className="file-upload">
          <input
            type="file"
            accept=".xlsx, .xls"
            onChange={handleFileChange}
            key={resetKey}
          />
          <Button
            variant="success"
            onClick={processFile}
            disabled={isLoading || !file}
          >
            {isLoading ? "Processing..." : "Process File"}
          </Button>
        </div>
        <div className="actions">
          <Button
            variant="danger"
            onClick={handleReset}
            disabled={isLoading}
          >
            {isLoading ? "Resetting..." : "Reset"}
          </Button>
          <Button
            variant="warning"
            onClick={handleSave}
            disabled={isLoading}
          >
            {isLoading ? "Saving..." : "Save Data"}
          </Button>
        </div>
      </div>
      <ConfirmationModal
        show={showConfirmationModal}
        onHide={() => setShowConfirmationModal(false)}
        onConfirm={() => {
          // Handle confirmation action
          setShowConfirmationModal(false);
        }}
        title="Confirm Action"
        body="Are you sure you want to perform this action?"
      />
      <ContentErrorsModal
        show={showContentErrorsModal}
        onHide={() => setShowContentErrorsModal(false)}
        errors={contentErrors}
        title="Content Errors"
        successMessage={successMessage}
        failureMessage={failureMessage}
      />
    </div>
  );
};

export default CompanionWidget;
